@import url('https://fonts.googleapis.com/css?family=Lato:300,400,700,900');

@import "../../packages/bootstrap-4.2.1/scss/bootstrap.scss";

* {
    margin: 0;
    padding: 0;
    font-family: 'Lato', sans-serif;
}

a {
    text-decoration: none;
    color: inherit;

    &:hover {
        text-decoration: none;
        color: inherit;
    }
}

.containerM {
    width: 1164px;
    margin: 0 auto;

    @media (max-width: 1164px) {
        width: auto;
    }
}

.wrap {
    width: 1600px;
    margin: 0 auto;

    @media (max-width: 1164px) {
        width: auto;
    }
}

.clearfix {
    clear: both;
    height: 0;
    width: 0;
}


/* ITEMS_HOVER_ADDTOCART+SHADOW */

.item_hover_wrap {
    position: relative;
    height: 370px;
    display: flex;
    flex-direction: column;
    color: #222222;

    &:hover {
        box-shadow: 0 5px 8px rgba(0, 0, 0, 0.16);
    }

    .item_hover_addToCart {
        position: absolute;
        background-color: rgba(58, 56, 56, 0.83);
        height: 280px;
        width: 261px;
        display: none;

        .addToCart_tablet {
            display: block;
            text-decoration: none;
            color: #ffffff;
            margin-top: 100px;
            margin-left: 70px;
            width: 125px;
            line-height: 40px;
            text-align: center;
            color: #ffffff;
            font-size: 13px;
            font-weight: 700;
            border: 1px solid white;
            border-radius: 3px;
        }
    }

    .item_hover_shadow {
        position: absolute;
        top: 280px;
        height: 85px;
        width: 261px;
        box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.16);
        background: white;
        z-index: -1;
        display: none;
    }
}



.item_hover_wrap:hover .item_hover_addToCart,
.item_hover_wrap:hover .item_hover_shadow {
    display: block;
}

/* /ITEMS_HOVER_ADDTOCART+SHADOW */


/* HEADER */

header {
    height: 100px;
    border-bottom: 1px solid #ececec;

    @media (max-width: 730px) {
        height: auto;
    }

    .headerInside {
        height: inherit;
        display: flex;
        flex-direction: row;
        align-items: center;

        @media (max-width: 730px) {
            flex-direction: column;
            height: auto;
        }

        .logo {
            line-height: 51px;
            width: 165px;
            background: url(../imgs/logo.png) no-repeat;
            color: #222222;
            font-size: 27px;
            font-weight: 300;
            box-sizing: border-box;
            padding-left: 72px;
            margin-right: 50px;

            @media (max-width: 730px) {
                padding-bottom: 15px;
                margin-right: 0;
            }

            span {
                color: #f16d7f;
                font-weight: 400;
            }
        }
    }
}

/* SEARCH */

.headerInside .searchWrap {
    display: flex;
    align-items: center;
    height: 40px;
    overflow: hidden;
    /*чтобы не торчали уголки блоков с скруглёнными границами*/
    border: 1px solid #e6e6e6;
    border-radius: 3px;

    .searchBrowse {
        height: inherit;
        width: 90px;
        box-sizing: border-box;
        padding: 11px;
        padding-right: 0;
        font-size: 14px;
        font-weight: 300;
        border-right: 1px solid #e6e6e6;
        background-color: #f9f9f9;

        a {
            color: #222222;
        }
    }

    input {
        height: inherit;
        box-sizing: border-box;
        padding-left: 16px;
        border: none;
    }

    button {
        height: inherit;
        background: inherit;
        width: 36px;
        border: none;
        border-left: 1px solid #e6e6e6;
    }
}

/* /SEARCH */

.headerInside .buttons {
    margin-left: auto;
    display: flex;

    @media (max-width: 730px) {
        margin-top: 15px;
        margin-left: 0;
    }
}

.headerInside .cartLogo {
    /*margin-left: auto;*/
    margin-right: 25px;
    align-self: center;
}

.headerInside .myAccount {
    background-color: #f16d7f;
    padding: 15px;
    color: #ffffff;
    font-size: 15px;
    font-weight: 400;
    border: none;
    border-radius: 3px;
    white-space: pre;
}

/* /HEADER */

/* TOP_NAVIGATION */

.topNav {
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;

    @media (max-width: 730px) {
        flex-direction: column;
        height: auto;
        padding: 15px 0;
    }

    a {
        display: block;
        text-decoration: none;
        color: #222222;
        font-size: 14px;
        font-weight: 700;
        margin-right: 30px;
        padding: 0 10px;

        @media (max-width: 730px) {
            margin: 0;
            padding-top: 5px;
        }

        &:hover {
            border-bottom: 3px solid #ef5b70;
            color: #f16d7f;
        }
    }
}

/* /TOP_NAVIGATION */

/* NEW_ARRIVALS */

.newArrivalsWrap {
    background-color: #f8f3f4;
    width: 1600px;
    height: 150px;

    @media (max-width: 1164px) {
        width: auto;
    }

    .newArrivals {
        display: flex;
        justify-content: space-between;
        height: inherit;
        align-items: center;

        @media (max-width: 520px) {
            flex-direction: column;
            justify-content: center;
        }

        h1 {
            color: #f16d7f;
            font-size: 24px;
            font-weight: 400;
        }

        h2 {
            display: inline-block;
            font-size: 14px;
            color: #636363;
            font-weight: 300;

            &:hover {
                color: #f16d7f;
                font-weight: 700;
            }
        }
    }
}

/* /NEW_ARRIVALS */


/* INDEX_MAIN */

/* TOP_BANNER */

.topBannerWrap {
    height: 610px;
    background: #e8e8e8;
}

.topBanner {
    background: url(../imgs/mainPageTopBanner.png) -180px;
    height: inherit;
    padding-top: 225px;
    box-sizing: border-box;

    @media (max-width: 992px) {
        background: url(../imgs/mainPageTopBannerCut.png) no-repeat;
    }
}

.topBanner .topBannerRectangle {
    height: 92px;
    width: 12px;
    background: url(../imgs/mainPageTopBannerRedRectangle.png);
    float: left;
}

.topBanner .topBannerText p:first-child {
    color: #222;
    line-height: 45px;
    font-size: 60px;
    letter-spacing: 1.5px;
}

.topBanner .topBannerText span:first-child {
    line-height: 65px;
    font-size: 40px;
    font-weight: 400;
    letter-spacing: 1px;
}

.topBanner .topBannerText span:last-child {
    color: #f16d7f;
    letter-spacing: 1px;
    font-size: 40px;
    font-weight: 400;
}

/* /TOP_BANNER */

/* FIRSTSECTION */

.firstSection {
    margin-top: 100px;
    box-sizing: border-box;
    min-height: 920px;
    display: flex;
    flex-wrap: wrap;
    

    @media (max-width: 1164px) {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    
    .pic1 {
        background: url(../imgs/mainPageFirstSection_Pic1.png) no-repeat;
        width: 560px; height: 542px;
        margin-bottom: 20px;
        p {
            width: 175px;
        }
    }
    .pic2 {
        background: url(../imgs/mainPageFirstSection_Pic2.png) no-repeat;
        width: 560px; height: 261px;
        p {
            width: 160px;
        }
    }
    .pic3 {
        background: url(../imgs/mainPageFirstSection_Pic3.png) no-repeat;
        width: 560px; height: 261px;
        p {
            width: 245px;
        }
    }
    .pic4 {
        background: url(../imgs/mainPageFirstSection_Pic4.png) no-repeat;
        width: 560px; height: 542px;
        position: relative;
        bottom: 280px;
        @media (max-width: 1164px) {
            position: initial;
        }
        p {
            width: 180px;
        }
    }
    
    .firstSectionPics {
        margin: 10px;
        box-sizing: border-box;
        display: flex;
        @media (max-width: 600px) {
            justify-content: center;
            align-items: center;
            width: 90%;
            background-position: center center;
            background-size: contain;
        }
        @media (max-width: 1164px) {
            margin: 20px 0;
        }
        p {
            position: relative;
            top: 25px;
            height: 80px;
            background-color: #ffffff;
            color: #222222;
            font-size: 18px;
            font-weight: 400;
            letter-spacing: 0.45px;
            span {
            color: #f16d7f;
            font-size: 30px;
            letter-spacing: 0.75px;
            }
            text-align: left;
            padding-left: 20px;
            @media (max-width: 600px) {
                position: initial;
                margin: 0;
            }
        }
    }
}

/* /FIRSTSECTION */

/* FEATURED_ITEMS */

.featuredItems {
    margin-top: 120px;
    h1 {
        color: #222222;
        font-size: 24px;
        font-weight: 700;
        margin-bottom: 0;
        text-align: center;
        letter-spacing: 0.75px;
    }

    h2 {
        margin-top: 10px;
        color: #9f9f9f;
        font-size: 14px;
        font-weight: 400;
        letter-spacing: 0.35px;
        text-align: center;
    }

    .featuredItemsPics {
        display: flex;
        flex-wrap: wrap;
        margin-top: 20px;
        justify-content: space-between;
        @media (max-width: 1163px) {
            justify-content: center;
        }

        .featuredItemsPic {
            margin: 0 15px;
            margin-top: 40px;
            height: 370px;
            display: flex;
            flex-direction: column;
            color: #222222;

            p {
                font-size: 13px;
                font-weight: 400;
                text-transform: uppercase;
                margin-top: 20px;
                margin-left: 15px;

                span {
                    display: block;
                    margin-top: 15px;
                    color: #f16d7f;
                    font-size: 16px;
                    font-weight: 700;
                }
            }
        }
    }
}



.featuredItems h3 {
    font-size: 16px;
    font-weight: 700;
    line-height: 54px;
    height: 50px;
    width: 210px;
    background: #f16d7f;
    margin: 0 auto;
    text-align: center;

    a {
        color: #ffffff;
    }
}

/* /FEATURED_ITEMS */

/* BOTTOM_BANNER */

.bottomBanner {
    height: 600px;
    position: relative;
    margin-top: 115px;
    box-sizing: border-box;
    background: #222224;
    background: url(../imgs/mainPage_bottomBannerPic.png) no-repeat;
    background-size: cover;
    background-position: center center;
    @media (max-width: 1200px) {
        height: auto;
        padding-top: 300px;
    }

    
    .bottomBannerHeader {
        position: absolute;
        top: 145px;
        left: 40px;
        color: #ffffff;
        font-size: 54px;
        font-weight: 700;
        letter-spacing: 1.5px; 

        span {
            color: #f16d7f;
            letter-spacing: 1.5px;
        }

        p {
            font-size: 32px;
            letter-spacing: 0.9px;
        }
    }
    .merits {
        position: absolute;
        top: 30px;
        right: 40px;
        width: 340px;
        
        
        @media (max-width: 1200px) {
            position: initial;
            display: flex;
            flex-wrap: wrap;
            width: auto;
            justify-content: center;
        }
        .merit {
            display: flex;
            flex-direction: row;
            &:not(:first-child) {
                margin-top: 30px;
            }
            @media (max-width: 1200px) {
                margin-top: 30px;
            }
        }
        .bottomBanner_meritsText {
            margin-left: 30px;
            width: 220px;
            p {
                margin-top: 15px;
                width: 220px;
                color: #fbfbfb;
                font-size: 14px;
                font-weight: 300;
                line-height: 24px;
                letter-spacing: 0.35px;
            }
            h4 {
                color: #fbfbfb;
                font-size: 20px;
                font-weight: 700;
                letter-spacing: 0.5px;
            }
        }
        .delivery {
            .truckPic {
                height: 40px;
                width: 47px;
                background: url(../imgs/mainPage_BottomBanner_truckPic.png) no-repeat;
            }
        }
        .sales {
            .salesPic {
                height: 40px;
                width: 47px;
                background: url(../imgs/mainPage_bottomBanner_salesPic.png) no-repeat;
            }
        }
        .qualityAssurance {
            .qualityAssurancePic {
                height: 40px;
                width: 47px;
                background: url(../imgs/mainPage_bottomBanner_crownPic.png);
            }
        }
    }
}

/* /BOTTOM_BANNER */

/* /INDEX_MAIN*/


/* SHOPPING_CART_MAIN */

/* CART */

/* CART_TOP */

.cart {
    margin-top: 90px;
}

.cart h4 {
    float: left;
    color: #222222;
    font-size: 13px;
    font-weight: 700;
    letter-spacing: -0.2px;
}

.cart .headers {
    display: flex;
    justify-content: space-between;
    width: 630px;
    float: right;
    color: #222222;
    font-size: 13px;
    font-weight: 700;
    letter-spacing: -0.2px;
    margin-bottom: 20px;
}

/* /CART_TOP */

/* CART_DETAILS */

.cart {
    .products {
        .product {
            min-height: 120px;
            padding: 20px 0;
            display: flex;
            justify-content: space-between;
            border-top: 1px solid rgba(128, 128, 128, 0.2);

            &:nth-child(3) {
                border-bottom: 1px solid rgba(128, 128, 128, 0.2);
            }

            @media (max-width: 567px) {
                flex-direction: column;
                max-width: 330px;
            }
        }

        .productPic {
            display: flex;
            float: left;

            img {
                height: 115px;
            }

            div {
                margin-left: 20px;
                padding-top: 15px;

                p {
                    margin-top: 40px;
                    font-size: 13px;
                    line-height: 20px;
                    color: #575757;
                    font-weight: 300;
                }

                h6 {
                    color: #222222;
                    font-size: 13px;
                    font-weight: 400;
                }
            }
        }

        .details {
            display: flex;
            justify-content: space-between;
            padding-left: 25px;
            box-sizing: border-box;

            @media (max-width: 567px) {
                justify-content: space-around;
                max-width: 330px;
                margin-top: 20px;

            }

            p {
                width: 54px;
                line-height: 30px;
                text-align: center;
                color: #656565;
                font-size: 13px;
                font-weight: 400;
                display: flex;
                flex-direction: column;
                margin: 0 15px;
                position: relative;
                height: 30px;

                &:nth-child(2) {
                    border: 1px solid #eaeaea;


                }

                &:last-child {
                    color: gray;
                    font-size: 15px;
                }

                span {
                    display: block;
                    width: 90px;
                    position: absolute;
                    bottom: 50px;
                    left: -14px;

                    @media (max-width: 567px) {
                        font-size: 7px;
                        bottom: 30px;
                        left: -30px;
                        color: #222222;
                        font-weight: 700;
                        letter-spacing: -0.2px;
                    }
                }
            }
        }
    }
}

/* /CART_DETAILS */

/* CART_MIDDLE_BUTTONS */

.cart_middleButtons {
    display: flex;
    justify-content: space-between;
    margin-top: 45px;

    @media (max-width: 500px) {
        flex-direction: column;
        margin-bottom: 20px;
    }

    a {
        display: block;
        width: 235px;
        line-height: 50px;
        border: 1px solid #eaeaea;
        background-color: #ffffff;
        color: #4a4a4a;
        font-size: 14px;
        font-weight: 700;
        text-align: center;

        &:first-child {
            @media (max-width: 500px) {
                margin-bottom: 20px;
            }
        }
    }
}



/* /CART_MIDDLE_BUTTONS */

/* CART_BOTTOM_SECTIONS*/

.cart_bottomSections {
    margin-top: 60px;
    display: flex;

    @media (max-width: 1199px) and (min-width: 992px) {
        justify-content: center;
    }

    legend {
        margin-bottom: 25px;
    }

    form {
        input {
            display: block;
            margin-top: 25px;
            width: 355px;
            height: 45px;
            border: 1px solid #eaeaea;
            padding-left: 20px;
            box-sizing: border-box;
            color: #b1b1b1;
            font-size: 13px;
            font-weight: 300;

            @media (max-width: 370px) {
                width: 80%;
            }
        }

        fieldset {
            border: none;
        }

        button {
            width: 100px;
            height: 35px;
            border: 1px solid #eaeaea;
            background-color: #ffffff;
            color: #4a4a4a;
            font-size: 11px;
            font-weight: 700;
            margin-top: 25px;
        }
    }

    .total_sumWrap {
        width: 360px;
        height: 214px;
        background-color: #f5f3f3;
        display: flex;
        justify-content: center;

        @media (max-width: 400px) {
            flex: 0 0 90%;
        }

        .total_sum {
            width: 275px;
            margin-top: 40px;
            display: flex;
            flex-direction: column;
            align-items: flex-end;

            @media (max-width: 400px) {
                align-items: center;
                flex: 0 0 100%;
            }

            h3 {
                color: #4a4a4a;
                font-size: 11px;
                font-weight: 400;
            }

            h4 {
                color: #222222;
                font-size: 16px;
                font-weight: 700;
                margin-top: 15px;

                span {
                    color: #f16d7f;
                }
            }

            .horizontal_line {
                margin-top: 20px;
                width: 275px;
                height: 1px;
                background-color: #e2e2e2;
            }

            a {
                width: 273px;
                line-height: 50px;
                background-color: #f16d7f;
                color: #ffffff;
                font-size: 16px;
                font-weight: 700;
                text-align: center;
                margin-top: 15px;

                @media (max-width: 570px) {
                    width: auto;
                }
            }
        }
    }
}

/* /CART_BOTTOM_SECTIONS*/

/* /CART */

/* /SHOPPING_CART_MAIN */


/* CHECKOUT_MAIN */

/* CHECKOUT-MAIN */

.checkoutWrap {
    height: auto;
}

.checkout {
    padding-top: 90px;
}

.checkoutWrap .shippingAddress_Flex {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.checkout summary {
    color: #222222;
    font-size: 16px;
    font-weight: 700;
    line-height: 70px;
    border-top: 1px solid #e8e8e8;
}

.shippingAddress summary:first-child {
    border: none;
}

/* TO_REGISTRATION */

.checkoutWrap .to_registration {
    margin-left: 35px;
}

@media (max-width: 730px) {
    .checkoutWrap .to_registration {
        padding-bottom: 40px;
        border-bottom: 1px solid gray;
    }
}

.checkoutWrap .to_registration h2 {
    color: #222222;
    font-size: 13px;
    font-weight: 400;
    margin-top: 40px;
}

.checkoutWrap .to_registration h3 {
    color: #b3b2b2;
    font-size: 13px;
    font-weight: 400;
    margin-top: 15px;
    margin-bottom: 25px;
}

.checkoutWrap .to_registration span {
    line-height: 35px;
}

.checkout input[name="checkout_or_reg"] {
    display: none;
}

.checkout label[for="guest"],
.checkout label[for="reg"] {
    background: url(../imgs/input_radio_empty.png) no-repeat 3px 3px;
    padding-left: 20px;
}

.checkout #guest:checked + label,
.checkout #reg:checked + label {
    background: url(../imgs/input_radio_checked.png) no-repeat 3px 3px;
}

.checkoutWrap .to_registration p {
    color: #b3b2b2;
    font-size: 13px;
    font-weight: 400;
    line-height: 20px;
    height: 70px;
}

.checkoutWrap .to_registration button {
    width: 180px;
    height: 50px;
    border: 1px solid #eaeaea;
    background-color: #ffffff;
    color: #4a4a4a;
    font-size: 14px;
    font-weight: 700;
}

/* /TO_REGISTRATION */

/* LOGIN */

.checkoutWrap .login {
    margin-left: 35px;
}

.checkoutWrap .login h2 {
    color: #222222;
    font-size: 13px;
    font-weight: 400;
    margin-top: 40px;
}

.checkoutWrap .login h3 {
    color: #b3b2b2;
    font-size: 13px;
    font-weight: 400;
    margin-top: 15px;
    margin-bottom: 33px;
}

.checkoutWrap .login p {
    display: inline;
    color: #222222;
    font-size: 12px;
    font-weight: 400;
    margin-bottom: 15px;
}

.checkoutWrap .login .red {
    color: #ff0d0d;
    font-size: 13px;
    font-weight: 400;
}

.checkoutWrap .login input {
    width: 390px;
    height: 45px;
    border: 1px solid #eaeaea;
    background-color: #ffffff;
    margin: 15px 0;

    @media (max-width: 500px) {
        width: 260px;
    }
}

.checkoutWrap .login button {
    width: 180px;
    height: 50px;
    border: 1px solid #eaeaea;
    background-color: #ffffff;
    color: #4a4a4a;
    font-size: 14px;
    font-weight: 700;
    margin-top: 27px;
}

.checkoutWrap .login .forgot {
    display: inline-block;
    color: #222222;
    font-size: 14px;
    font-weight: 400;
    margin-left: 30px;
    margin-bottom: 80px;
}

/* /LOGIN */

/* /CHECKOUT-MAIN */


/* SINGLE_PAGE-MAIN */

/* SINGLE_PAGE_BANNER */

.singlePageBannerWrap {
    background: url(../imgs/SinglePage_Main_Background.png);
    height: 780px;

    .sliders {
        height: inherit;
        position: absolute;
        width: inherit;
        display: flex;
        flex-shrink: 0;
        justify-content: space-between;

        @media (max-width: 1164px) {
            width: 100%;
        }

        .sliderImg {
            position: relative;
            top: calc(50% - 47px /2);

            a:hover {
